require("../Sass/Form/Select.scss");
let React=require("react");
let unit=require("../libs/unit.js");
var FetchModel=require("../Model/FetchModel.js");
var validation=require("../Lang/validation.js");
let setStyle=require("../Mixins/setStyle.js");
var validate=require("../Mixins/validate.js");
var showUpdate=require("../Mixins/showUpdate.js");
var shouldComponentUpdate=require("../Mixins/shouldComponentUpdate.js");
var Label=require("../Unit/Label.jsx");
var Message=require("../Unit/Message.jsx");
var ClickAway=require("../Unit/ClickAway.js");
let Select=React.createClass({
    mixins:[setStyle,validate,showUpdate,shouldComponentUpdate,ClickAway],
    PropTypes:{
        name:React.PropTypes.string.isRequired,//
        label:React.PropTypes.oneOfType([React.PropTypes.string,React.PropTypes.element,React.PropTypes.node]),//
        width:React.PropTypes.number,//
        height:React.PropTypes.number,//
        value:React.PropTypes.oneOfType([React.PropTypes.number,React.PropTypes.string]),//,
        text:React.PropTypes.oneOfType([React.PropTypes.number,React.PropTypes.string]),//
        placeholder:React.PropTypes.string,//
        readonly:React.PropTypes.bool,//
        required:React.PropTypes.bool,//
        onlyline:React.PropTypes.bool,//
        hide:React.PropTypes.bool,//
        regexp:React.PropTypes.string,//
        invalidTip:React.PropTypes.string,//
        style:React.PropTypes.object,//style
        className:React.PropTypes.string,//class
        size:React.PropTypes.oneOf([
            "none",
            "default",
            "large",//,two
            "two",
            "three",
            "onlyline"
        ]),//
        position:React.PropTypes.oneOf([
            "left",
            "default",
            "right"
        ]),//

        //
        min:React.PropTypes.number,//
        max:React.PropTypes.number,//
        onClick: React.PropTypes.func,//

        //
        multiple:React.PropTypes.bool,//
        valueField: React.PropTypes.string,//
        textField:React.PropTypes.string,//
        url:React.PropTypes.string,//ajax
        params:React.PropTypes.object,//
        dataSource:React.PropTypes.string,//ajax,null
        data:React.PropTypes.array,//
        extraData:React.PropTypes.array,//,url
        onSelect: React.PropTypes.func,//value,text,data




    },
    getDefaultProps:function() {
        return{
            name:"",
            label:null,
            width:null,
            height:null,
            value:"",
            text:"",
            placeholder:"",
            readonly:false,
            required:false,
            onlyline:false,
            hide:false,
            regexp:null,
            invalidTip:null,
            style:null,
            className:null,
            size:"default",
            position:"default",
            //
            min:null,
            max:null,
            onClick:null,
            //
            multiple:false,
            valueField:"value",
            textField:"text",
            url:null,
            params:null,
            dataSource:"data",
            data:null,
            extraData:null,
            onSelect:null,
            writable:false,

        };
    },
    getInitialState:function() {
        var newData=[];var text=this.props.text;
        if(this.props.data&&this.props.data instanceof  Array)
        {
            for(let i=0;i<this.props.data.length;i++)
            {
                let obj=this.props.data[i];
                obj.text=this.props.data[i][this.props.textField];
                obj.value=this.props.data[i][this.props.valueField];
                if(obj.value==this.props.value)
                {
                    text=obj.text;//value
                }
                newData.push(obj);
            }
        }

        return {
            hide:this.props.hide,
            params:unit.clone(this.props.params),//
            data:newData,
            value:this.props.value,
            text:text,
            show:false,//
            multiple:this.props.multiple,
            min:this.props.min,
            max:this.props.max,
            readonly:this.props.readonly,

            //
            required:this.props.required,
            validateClass:"",//
            helpShow:"none",//
            helpTip:validation["required"],//
            invalidTip:"",
            filterValue:null,//
        }
    },
    componentWillReceiveProps:function(nextProps) {
        /*
         this.isChange :,value,text,
         */

        var value=this.isChange?this.state.value: nextProps.value;
        var text = this.isChange?this.state.text: nextProps.text;
        this.isChange=false;//
        var newData = null;
        if(nextProps.data!=null&&nextProps.data instanceof  Array &&(!nextProps.url||nextProps.url=="")) {//url,
            newData=[];
            //,
            for (let i = 0; i < nextProps.data.length; i++) {
                let obj = nextProps.data[i];
                obj.text = nextProps.data[i][this.props.textField];
                obj.value = nextProps.data[i][this.props.valueField];
                if (obj.value == nextProps.value) {
                    text = obj.text;//value
                }
                newData.push(obj);
            }

        }
        else {//url
            newData = this.state.data;//
            if (this.showUpdate(nextProps.params)) {//
                this.loadData(this.props.url, nextProps.params);//
            }
            else {

            }
        }

        this.setState({
            hide:nextProps.hide,
            value:value,
            text:text,
            data: newData,
            params:unit.clone( nextProps.params),
            multiple: nextProps.multiple,
            min: nextProps.min,
            max: nextProps.max,
            readonly: nextProps.readonly,
            required: nextProps.required,
            validateClass:"",//
            helpTip:validation["required"],//
            filterValue:null,
        })

    },
    componentWillMount:function() {//url,
        this.loadData(this.props.url,this.state.params);//
    },
    componentDidMount:function () {

        this.registerClickAway(this.hideOptions, this.refs.select);//
    },
    componentDidUpdate:function() {
        if(this.isChange==true)
        {//,
            if( this.props.onSelect!=null)
            {
                this.props.onSelect(this.state.value,this.state.text,this.props.name,this.rowData);
            }
        }
    },

    loadData:function(url,params) {
        if(url!=null&&url!="")
        {
            if(params==null)
            {
                var fetchmodel=new FetchModel(url,this.loadSuccess,null,this.loadError);
                unit.fetch.get(fetchmodel);
            }
            else

            {
                var fetchmodel=new FetchModel(url,this.loadSuccess,params,this.loadError);
                unit.fetch.post(fetchmodel);
            }
            console.log("select",fetchmodel);
        }
    },
    loadSuccess:function(data) {//
        var realData=data;
        if(this.props.dataSource==null) {
        }
        else {
            realData=unit.getSource(data,this.props.dataSource);
        }
        var newData=[];var text=this.state.text;
        for(let i=0;i<realData.length;i++)
        {
            let obj=realData[i];//
            obj.text=realData[i][this.props.textField];
            obj.value=realData[i][this.props.valueField];
            if(obj.value==this.state.value)
            {
                text=obj.text;//value
            }
            newData.push(obj);
        }
        if(this.props.extraData==null||this.props.extraData.length==0)
        {
            //
        }
        else
        {
            //
            for(let i=0;i<this.props.extraData.length;i++)
            {
                let obj={};
                obj.text=this.props.extraData[i][this.props.textField];
                obj.value=this.props.extraData[i][this.props.valueField];
                if(obj.value==this.state.value)
                {
                    text=obj.text;//value
                }
                newData.unshift(obj);
            }
        }
        window.localStorage.setItem(this.props.name+'data' ,JSON.stringify(newData));//

        this.setState({
            data:newData,
            value:this.state.value,
            text:text,
        })
    },
    loadError:function(errorCode,message) {//
        console.log("select-error",errorCode,message);
        Message. error(message);
    },
    showOptions:function(type) {//
        if (this.state.readonly) {
            return;
        }
        if (this.props.onClick != null) {
            this.props.onClick();
        }
        this.setState({
            show: type==1?!this.state.show:true,
        });
        this.bindClickAway();//
    },
    hideOptions:function (event) {
        this.setState({
            show: false
        });
        this.unbindClickAway();//
    },

    onSelect:function(value,text,rowData) {//
        this.isChange=true;//,value,text,
        this.rowData=rowData;//
        var newvalue = "";
        var newtext = "";
        if(value==undefined)
        {
            console.error("valueField")
        }
        if(text==undefined)
        {
            console.error("textField");
        }
        if(this.state.multiple) {

            var oldvalue =[];
            var oldtext =[];
            if(this.state.value)
            {
                oldvalue=this.state.value.toString().split(",");
                oldtext=this.state.text.toString().split(",");
            }
            if (oldvalue.indexOf(value.toString()) > -1) {//
               oldvalue.splice(oldvalue.indexOf(value.toString()),1);
                oldtext.splice(oldvalue.indexOf(value.toString()),1);
                newvalue=oldvalue.join(",");
                newtext=oldtext.join(",");
            }
            else {//
                if(this.state.value)
                {
                    newvalue = this.state.value + "," + value;
                    newtext = this.state.text + "," + text;
                }
                else
                {
                    newvalue = value;
                    newtext =  text;
                }

            }
            this.setState({
                value:newvalue,
                text:newtext,
            });
        }
        else
        {
            var newvalue = value;
            var newtext = text;
            this.setState({
                show:false,
                value:newvalue,
                text:newtext,
                filterValue:null,
            });
        }
        this.validate(newvalue);//

    },
    getComponentData:function() {//
        return this.state.data;
    },
    onBlur:function () {
        if(this.state.filterValue!=null) {
            this.setState({
                filterValue: null
            })
        }

        this.refs.label.hideHelp();//
    },
    filterChangeHandler:function(event) {//
        let filterData=[]; let notfilterdData=[];
        this.state.data.map((item,index)=>{
            if(event.target.value!=""&&item.text.toLowerCase().indexOf(event.target.value.toLowerCase())>-1)
            {
                filterData.unshift({name:item,index:item.text.toLowerCase().indexOf(event.target.value.toLowerCase())});
            }
            else
            {
                notfilterdData.push((item));
            }
        })
        //
        for(var i=0;i<filterData.length-1;i++){
            for(var j=0;j<filterData.length-i-1;j++){
                if(filterData[j].index>filterData[j+1].index){
                    var swap=filterData[j];
                    filterData[j]=filterData[j+1];
                    filterData[j+1]=swap;
                }
            }
        }
        let newFitlerData=[];
        for(var i=0;i<filterData.length;i++) {
          newFitlerData.push(filterData[i].name);
        }
        this.setState({
            data:newFitlerData.concat(notfilterdData),
            filterValue:event.target.value,

        })

    },
    clearHandler:function() {//
        if(this.props.onSelect!=null)
        {
            this.props.onSelect("","",this.props.name,null);
        }
        else
        {
            this.setState({
                value:null,
                text:null,
            })
        }
    },
    render:function() {

        var size=this.props.onlyline==true?"onlyline":this.props.size;//
        var componentClassName=  "wasabi-form-group "+size;//
        var style =this.setStyle("input");//
        var controlStyle=this.props.controlStyle?this.props.controlStyle:{};
        controlStyle.display = this.state.hide == true ? "none" : "block";
        let inputProps=
        {
            readOnly:this.state.readonly==true?"readonly":null,
            style:style,
            name:this.props.name,
            placeholder:(this.props.placeholder===""||this.props.placeholder==null)?this.state.required?"":"":this.props.placeholder,
            className:"wasabi-form-control  "+(this.props.className!=null?this.props.className:""),
            title:this.props.title,

        }//
        var control=null;
        if(this.state.data&&this.state.data.length>0) {
            control = <ul style={{display:this.state.show==true?"block":"none"}}  >
                {
                    this.state.data.map((child, i)=> {
                        var checked = false;
                        if ((this.state.value != null && this.state.value != undefined && this.state.value != ""&&child.value!="") && (("," + this.state.value.toString()).indexOf("," + child.value) > -1)) {
                            checked = true;
                        }
                        else if (this.state.value == "" && child.value == "") {
                            checked = true;
                        }
                        return (
                            <li key={"li"+i} className={checked==true?"active":""}
                                onClick={this.onSelect.bind(this, child.value, child.text,child)}>{child.text}</li>
                        )
                    })

                }
            </ul>;
        }

        return (
            <div className={componentClassName+this.state.validateClass}  ref="select" style={ controlStyle} >
                <Label name={this.props.label} ref="label" hide={this.state.hide} required={this.state.required}></Label>
                <div className={ "wasabi-form-group-body"} >
                    <div className={"nice-select "}  style={style}    >
                        <i className={"picker-clear"} onClick={this.clearHandler} style={{display:this.state.readonly?"none":(this.state.value==""||!this.state.value)?"none":"inline"}}></i>
                        <i className={"icon "+(this.state.show?"rotate":"")} onClick={this.showOptions.bind(this,1)}></i>
                        <input type="text" {...inputProps}  value={this.state.filterValue!=null?this.state.filterValue:this.state.text} onClick={this.showOptions.bind(this,2)} onBlur={this.onBlur}    onChange={this.filterChangeHandler}  />

                        {
                            control
                        }
                    </div>
                    <small className={"wasabi-help-block "+this.props.position} style={{display:(this.state.helpTip&&this.state.helpTip!="")?this.state.helpShow:"none"}}><div className="text">{this.state.helpTip}</div></small>
                </div>
            </div>

        );

    }

});
module.exports=Select;


